.App {
  font-family: sans-serif;
  text-align: center;
}
input,
button {
  margin: 0 3px;
}
.display-screen {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.display-screen div:not(div.actions.btn) {
  box-shadow: 0 0 10px -4px #00000080;
  padding: 10px;
  margin: 10px;
  border-radius: 6px;
}
div.actions.btn {
  display: flex;
  align-items: center;
  box-shadow: none;
}
